import SearchBar from '@/components/SearchBar'

// 每页条数
const PAGE_SIZE = 6;

export default {
  components: {
    SearchBar
  },
  data () {
    return {
      menuList: ['综合', '销量', '价格'],
      // 记住当时选中的过滤栏的菜单
      activeIndex: 0,
      keyword: '',
      // 商品列表
      goodsList: [],
      isLastPage: false
    };
  },
  onLoad (options) {
    // 声明
    this.pageNum = 1
    this.isRequesting = false

    console.log(options);
    this.keyword = options.catName || ''
    // 根据这个关键字去搜索
    this.queryGoods();
  },
  // 下拉刷新触发
  onPullDownRefresh () {
    // 加载第一页的数据
    this.reload();
  },
  // 触发上拉
  onReachBottom () {
    // 加载下一页
    this.pageNum++;
    this.queryGoods();
  },
  methods: {
    // 输入框的搜索
    searchHandler (inputVal) {
      this.keyword = inputVal
      this.reload()
    },
    // 商品详情
    toItem (goodsId) {
      uni.navigateTo({ url: '/pages/item/item?goodsId=' + goodsId });
    },
    reload () {
      // 加载第一页的数据
      this.pageNum = 1;
      this.isLastPage = false
      // 清空搜索结果
      this.goodsList = [];
      this.queryGoods();
    },
    changeIndex (index) {
      this.activeIndex = index;
    },
    async queryGoods () {
      // 请求前,如果请求中或者如果是最后一页，就返回
      if (this.isRequesting || this.isLastPage) {
        return;
      }
      // 请求前,锁住
      this.isRequesting = true;

      let data = await this.$request({
        url: '/api/public/v1/goods/search',
        showLoading: false,
        data: {
          query: this.keyword,
          pagenum: this.pageNum,
          pagesize: PAGE_SIZE
        }
      });
			// 请求结束后,主动下拉动画
			uni.stopPullDownRefresh()
			
      this.goodsList = [...this.goodsList, ...data.goods];
      // 每一次请求完后,我可以判断是否是最后一页
      if (this.goodsList.length === data.total) {
        this.isLastPage = true;
      }
      // 请求后,释放
      this.isRequesting = false;
    }
  }
};
